import { Component, OnInit} from '@angular/core';
import { HttpClient } from '@angular/common/http';





@Component({
  selector: 'app-bienvenidos',
  templateUrl: './bienvenidos.component.html',
  styleUrls: ['./bienvenidos.component.css']
})
export class BienvenidosComponent implements OnInit {
  bienvenidosImagenes: any[] = [

    { nombre: 'ADMINISTRACIÓN MAESTRA', imagen: 'assets/Sima.jpg' },
    { nombre: 'ADMINISTRACIÓN DE CRÉDITOS Y CUENTAS POR COBRAR', imagen: 'assets/Tima.jpg' },
    
  ];
 

  public news: any[]=[];
  public relevantnew:any[]=[];
 
  public imagen:String;
  public imagenrele:String;
  public numero:any;
  public clasi:any[]=[];
  contador:number=0;
  contador1:number=0;
  indice1:any;
  indice2:any;
  public news1:any[]=[];
  public news2:any[]=[];
   
      
 
  constructor(private http: HttpClient) {
    
    this.getTerraNews().then(Noticias => {
      this.numero=Noticias.items.length 
  
      
   for (let i = 0; i < this.numero ; i++) {
    this.clasi[i]=Noticias.items[i]['labels']
    
    
    if (this.clasi[i] =='Noticia' ) {
      if((i-this.contador)<=4){
      this.news[i-this.contador]= Noticias.items[i]  
      this.indice1 = this.news[i-this.contador]['content'].indexOf('https');
      this.indice2 = this.news[i- this.contador]['content'].indexOf('" ima');
      this.news1[i-this.contador]=this.news[i-this.contador]['content'].substring(this.indice2,this.indice1); 
      }
        this.contador1=this.contador1 +1;
      
    }
    
     else{
       if((i-this.contador1<=4)){
      
      this.relevantnew[i-this.contador1]=Noticias.items[i];
      this.indice1 = this.relevantnew[i-this.contador1]['content'].indexOf('https');
      this.indice2 = this.relevantnew[i-this.contador1]['content'].indexOf('" ima');
      this.news2[i-this.contador1]=this.relevantnew[i-this.contador1]['content'].substring(this.indice2,this.indice1);
       }
      this.contador=this.contador +1;
      
    }         
  }    
  
    })
    
      }


  public ngOnInit() {    
    this.getTerraNews();
    
  }

  public getTerraNews(): Promise<any> {
    return new Promise((resolve) => {
      this.http.get(
        'https://www.googleapis.com/blogger/v3/blogs/4904488018714938692/posts?key=AIzaSyArCwpD0r8wfpkTcnQrlHmn7R2kp38PKbk'
      ).subscribe(
        data => { resolve(data) },
        error => { resolve("ERROR") }

      )
    });
  }
 

  imagen1(a:number){
    
            this.imagen=this.news1[a]
            return this.imagen  
         
   
    
    
  }
  imagen2(a:number){
    
    this.imagenrele=this.news2[a]
    return this.imagenrele  



}

 



}





