import {LocationStrategy, HashLocationStrategy} from '@angular/common';
import { BrowserModule } from '@angular/platform-browser';
import { HttpClientModule } from '@angular/common/http';
import { NgModule } from '@angular/core';
import { Routes, RouterModule, Router } from '@angular/router';
import { AppComponent } from './app.component';
import { BienvenidosComponent } from './bienvenidos/bienvenidos.component'
import { NosotrosComponent } from './nosotros/nosotros.component';
import { MenuComponent } from './menu/menu.component';
import { RecursosComponent } from './recursos/recursos.component';
import { EquipoComponent } from './equipo/equipo.component';
import { ExperienciaComponent } from './experiencia/experiencia.component';
import { CalificacionComponent } from './calificacion/calificacion.component';
import { AEDComponent } from './aed/aed.component';
import { ContactoComponent } from './contacto/contacto.component';
import { ADPComponent } from './adp1/adp1.component';
import { HilcoTerracotaComponent } from './hilco-terracota/hilco-terracota.component';
import { NoticiasComponent } from './noticias/noticias.component';
import { ServiceWorkerModule } from '@angular/service-worker';
import { environment } from '../environments/environment';
import localePy from '@angular/common/locales/es';

import { registerLocaleData } from '@angular/common';
registerLocaleData(localePy, 'es');



const rutas: Routes = [
  { path: '', component: BienvenidosComponent},
 {path: 'Nosotros', component: NosotrosComponent},
{path: 'Recursos', component: RecursosComponent},
{path: 'Equipo', component: EquipoComponent},
{path: 'Experiencia', component: ExperienciaComponent},
{path: 'Calificacion', component: CalificacionComponent},
{path: 'Activos en Admon', component: AEDComponent},
{path: 'Contacto', component: ContactoComponent},
{path: 'Aviso-de-Privacidad', component: ADPComponent},
{path: 'Administradora-de-activos-Terracota-S.A-de-C.V', component: HilcoTerracotaComponent},
{path:'Noticias/:id', component: NoticiasComponent}



];
@NgModule({
  declarations: [
    AppComponent,
    BienvenidosComponent,    
    NosotrosComponent,
     MenuComponent,
     RecursosComponent,
     EquipoComponent,
     ExperienciaComponent,
     CalificacionComponent,
     AEDComponent,
     ContactoComponent,
     ADPComponent,
     HilcoTerracotaComponent,
     NoticiasComponent
  ],
  imports: [
    BrowserModule,
    RouterModule.forRoot(rutas, {useHash: true}),
       RouterModule,
       
       HttpClientModule,
       
       ServiceWorkerModule.register('ngsw-worker.js', { enabled: environment.production }),
       
       
       
       
  ],
  providers: [{provide: LocationStrategy, useClass: HashLocationStrategy}],
  bootstrap: [AppComponent]
})
export class AppModule { }
