import { Component, OnInit } from '@angular/core';
import { HttpClient } from '@angular/common/http';
import { ActivatedRoute } from '@angular/router';





@Component({
  selector: 'app-noticias',
  templateUrl: './noticias.component.html',
  styleUrls: ['./noticias.component.css']
})
export class NoticiasComponent implements OnInit {




  public news: any;
  public href: string;
  public imagen: string;
  public imagen1: any[] = [];
  public new: string;
  public indice: string;
  public numero: any;
  public contenido: any;
  public news1: any[] = [];
  public news2: any[] = [];
  public noti: any[] = [];
  titulo: string;
  indice1: any;
  indice2: any;
  indice3: any;
  contador: number = 0;
  num: number = 0;


  constructor(private http: HttpClient, private router: ActivatedRoute) {
    this.getTerraNews().then(Noticias => {
      this.href = this.router.snapshot.params.id;
      this.numero = Noticias.items.length;
      this.news = Noticias.items;

      for (let i = 0; i < this.numero; i++) {
        this.news1[i] = Noticias.items[i]['title']
        this.news2[i] = Noticias.items[i]['labels']
     



        if (this.href == this.news1[i]) {
         
            this.titulo = this.news[i]
            this.indice1 = this.news[i]['content'].indexOf('https');
            this.indice2 = this.news[i]['content'].indexOf('" ima');
            this.imagen = this.news[i]['content'].substring(this.indice2, this.indice1);
            this.indice3 = this.news[i]['content'].indexOf("<br");
            document.getElementById("contenido").innerHTML = this.news[i]['content'].substring(0, this.indice3);           
            this.contador = this.contador + 1;
                   

        }

        else if (this.num <= 2) {


          this.indice1 = this.news[i]['content'].indexOf('https');
          this.indice2 = this.news[i]['content'].indexOf('" ima');
          this.imagen1[i - this.contador] = this.news[i]['content'].substring(this.indice2, this.indice1);
          this.noti[i - this.contador] = this.news1[i];
          this.num = this.num + 1;

        }
        else {

        }
      }
    })
  }

  ngOnInit() {

    this.getTerraNews();
  }
  public getTerraNews(): Promise<any> {
    return new Promise((resolve) => {
      this.http.get(
        'https://www.googleapis.com/blogger/v3/blogs/4904488018714938692/posts?key=AIzaSyArCwpD0r8wfpkTcnQrlHmn7R2kp38PKbk'
      ).subscribe(
        data => { resolve(data) },
        error => { resolve("ERROR") }

      )
    });
  }




}

